import React from 'react';

function Recommendation() {
    const currentMonth = new Date().getUTCMonth()

    if (currentMonth >= 0 && currentMonth <= 2) {
        return (
            <p>
                Nous sommes en hiver ⛄ pensez à tailler vos arbustes.
            </p>
        )
    } else if (currentMonth >= 3 && currentMonth <= 5) {
        return (
            <p>
                Vive le printemps! Une belle saison pour rempoter vos plantes 🌱
            </p>
        )
    } else if (currentMonth >= 6 && currentMonth <= 8) {
        return (
            <p>
                C'est l'été! N'oubliez pas d'arroser régulièrement vos petites plantes 🚿
            </p>
        )
    } else {
        return (

            <p>
                Déjà l'Automne 🍂 n'oubliez pas de protéger vos plantes fragiles avant l'hiver!
            </p>

        )
    }

}

export default Recommendation
